import Queue from './queue';
import Stack from './stack';
import { GrammarSchema } from './types/grammar-schema';

export function process(
  stack: Stack<string>,
  entryQueue: Queue<string>,
  grammar: GrammarSchema
): Record<string, any>[] {
  const actions = [];

  while (!entryQueue.isEmpty() && !stack.isEmpty()) {
    const currentToken = entryQueue.peek();
    const currentSymbol = stack.peek();

    const step: { [x: string]: any } = {};
    step['currentToken'] = currentToken;
    step['currentSymbol'] = currentSymbol;
    step['stack'] = [...stack.getItems()];
    step['queue'] = [...entryQueue.getItems()];

    if (!currentSymbol || !currentToken)
      throw new Error(
        'Invalid state: empty stack or queue'
      );

    if (currentSymbol === '$' && currentToken === '$') {
      step['action'] = 'Aceito';
      actions.push(step);
      entryQueue.dequeue();
      stack.pop();
      break;
    }

    if (currentSymbol === '$' && currentToken !== '$') {
      step['action'] =
        'Erro: a pilha est vazia mas a entrada no';
      actions.push(step);
      stack.clear();
      entryQueue.clear();
      break;
    }

    if (currentToken === '$' && currentSymbol !== '$') {
      step['action'] =
        'Erro: a entrada est vazia mas a pilha no';
      actions.push(step);
      stack.clear();
      entryQueue.clear();
      break;
    }

    if (
      grammar.terminals.includes(currentSymbol) &&
      grammar.terminals.includes(currentToken)
    ) {
      if (currentToken === currentSymbol) {
        step['action'] = `L '${currentToken}'`;
        actions.push(step);
        entryQueue.dequeue();
        stack.pop();
        continue;
      } else {
        step['action'] =
          'Erro: smbolo terminal no corresponde ao token de entrada';
        actions.push(step);
        stack.clear();
        entryQueue.clear();
        break;
      }
    }

    if (currentSymbol in grammar.parsingTable) {
      if (
        currentToken in grammar.parsingTable[currentSymbol]
      ) {
        const production =
          grammar.parsingTable[currentSymbol][currentToken];

        step['action'] =
          `Expande: ${currentSymbol} -> ${production}`;
        actions.push(step);

        stack.pop();
        if (production !== '') {
          const productionSymbols = production
            .split('')
            .reverse();

          for (const symbol of productionSymbols) {
            stack.push(symbol);
          }
        }
      }
    } else {
      step['action'] =
        'Erro: smbolo no encontrado na tabela de parsing';
      actions.push(step);
      stack.clear();
      entryQueue.clear();
      break;
    }
  }

  return actions;
}
